import './Ground.css';
import React, { useState, useEffect } from 'react';
import Box from './comp/Box';
import BoxGroup from './comp/BoxGroup';
import imgData from './comp/ImgData'
// constants
let START_POS = { x: 72, y: 178, img: imgData.f2, trans: 'all 0.3s linear', class: '' };

let WINER_POS = {
    "x": 552,
    "y": 125,
    img: imgData.f2,
};
let START_POS_ARR = ([
    {
        "x": 1017,
        "y": 0,
        bg: 'blue',
        id: '1',
        img: imgData.m1,
    }, {
        "x": 1056,
        "y": 80,
        bg: 'green',
        id: '2',
        img: imgData.m2,

    }, {
        "x": 1041,
        "y": 150,
        bg: 'yellow',
        id: '3',
        img: imgData.m3,

    },
    {
        "x": 1048,
        "y": 250,
        bg: 'purple',
        id: '4',
        img: imgData.m4,
    },
    {
        "x": 1048,
        "y": 350,
        bg: 'purple',
        id: '5',
        img: imgData.m5,
    },
    {
        "x": 1048,
        "y": 450,
        bg: 'purple',
        id: '6',
        img: imgData.m6,
    },
]);

const Ground = () => {
    // ############# state
    // boxState
    const [boxPos, setBoxPos] = useState(START_POS);
    const [posArr, setPosArr] = useState([
        {
            "x": 1017,
            "y": 0,
            bg: 'blue',
            id: '1',
            img: imgData.m1,
        }, {
            "x": 1056,
            "y": 80,
            bg: 'green',
            id: '2',
            img: imgData.m2,

        }, {
            "x": 1041,
            "y": 150,
            bg: 'yellow',
            id: '3',
            img: imgData.m3,

        },
        {
            "x": 1048,
            "y": 250,
            bg: 'purple',
            id: '4',
            img: imgData.m4,
        },
        {
            "x": 1048,
            "y": 350,
            bg: 'purple',
            id: '5',
            img: imgData.m5,
        },
        {
            "x": 1048,
            "y": 450,
            bg: 'purple',
            id: '6',
            img: imgData.m6,
        },
    ]);
    // over
    const [isOver, setIsOver] = useState(false);
    // move flower
    const [isFlowerMove, setIsFlowerMove] = useState(false);
    // func
    const moveBox = (ev) => {
        // return if game over
        if (isOver) {
            reStart()
            return;
        };
        setIsOver(true);
        // let x = ev.clientX - ev.nativeEvent.target.offsetLeft;
        // let y = ev.clientY - ev.nativeEvent.target.offsetLeft;
        // console.log("Pos :", { x, y });
        let randomPosObj = posArr[randomNum(posArr.length)];
        setBoxPos({ ...randomPosObj, img: imgData.f2, });

        // reset post
        resetAterSecond(randomPosObj);
    }
    const randomNum = (len) => {
        return Math.floor(Math.random() * len);
    }
    const resetAterSecond = (randomPosObj) => {
        setTimeout(() => {
            setBoxPos(WINER_POS);
            selectedAtStart(randomPosObj);
        }, 1000);
    }
    const selectedAtStart = (randomPosObj) => {
        setPosArr(prev => {
            let newPrev = posArr.map(item => {
                if (item.id === randomPosObj.id) {
                    return ({ x: WINER_POS.x - 50, y: WINER_POS.y, id: item.id, bg: item.bg, img: item.img });
                }
                return item;
            });
            return newPrev;
        })
    }
    const reStart = () => {
        setIsOver(false);
        setPosArr(START_POS_ARR);
        setBoxPos(START_POS);
    }
    const moveFlower = (ev) => {
        if (!isFlowerMove) return;
        let x = ev.clientX - 90;
        let y = ev.clientY - 60;
        console.log({ x, y });
        // console.log(ev.nativeEvent.target.offsetLeft-20);
        // console.log(ev.nativeEvent.target.offsetTop-20);
        setBoxPos({ ...START_POS, x, y, trans: 'all 0s linear' });
    }
    const stopFlower = (ev) => {
        setIsFlowerMove(false);
        moveBox();
    }
    const startFlower = (ev) => {
        setIsFlowerMove(true);
        setBoxPos(prev => ({ ...prev, class: 'activeFlower' }))
    }
    // const allowMoveFlower = (ev)=>{

    // }

    // ###########effec
    // set pos to zero 
    // useEffect(() => {

    // }, [boxPos])


    return (
        <div className='ground' >
            {/* <div className="toolBox">
                <button className='btn btn-primary' onClick={reStart}>Retery</button>
            </div> */}
            <div className="groundBox" onMouseMove={moveFlower} onMouseUp={() => setIsFlowerMove(false)}>
                {/* <button className='throwBtn' onClick={moveBox} title='Throw and Retery Button'>
                    <img src={imgData.throwIcon} alt="" />
                </button> */}

                <Box boxPos={{ x: 10, y: 150, img: imgData.female }} />

                {/* <Box boxPos={boxPos}  /> */}

                <div onDragStart={(ev) => ev.preventDefault()} className='box'
                    style={{ animation: isFlowerMove ? `moveCircle 0.3s linear infinite` : 'none', transition: boxPos.trans, left: `${boxPos.x}px`, top: `${boxPos.y}px` }}
                    onMouseUp={stopFlower} onMouseDown={startFlower}  >
                    <img onDragStart={(ev) => ev.preventDefault()} src={boxPos.img} className={isFlowerMove ? `noActiveFlower boxImg` : `activeFlower boxImg`} alt="" />
                </div>

                <BoxGroup posArr={posArr} />
            </div>

            <div className="infoBox">
                <button className="btn info">
                    <img src={imgData.info} alt="" />
                    <div className="infoContent">
                        <p>
                            1-Grab flower and throw toward boys.
                        </p>
                        <p>
                            2-You could return flower to girl.
                        </p>
                    </div>
                </button>
                <button onClick={reStart} className="btn info">
                    <svg viewBox="0 0 24 24" preserveAspectRatio="xMidYMid meet" focusable="false" ><g>
                        <path d="M12 4V1L8 5l4 4V6c3.31 0 6 2.69 6 6 0 1.01-.25 1.97-.7 2.8l1.46 1.46C19.54 15.03 20 13.57 20 12c0-4.42-3.58-8-8-8zm0 14c-3.31 0-6-2.69-6-6 0-1.01.25-1.97.7-2.8L5.24 7.74C4.46 8.97 4 10.43 4 12c0 4.42 3.58 8 8 8v3l4-4-4-4v3z">
                        </path>
                    </g></svg>
                </button>
            </div>
        </div>
    )
}

export default Ground